import React, { useState, useEffect, useRef, useMemo, useCallback } from 'react';
import { 
  Layout, 
  Typography, 
  Button, 
  Card, 
  Table, 
  Space, 
  Tag, 
  ConfigProvider,
  Input,
  Switch,
  Avatar,
  Tooltip,
  Empty,
  Spin,
  Tabs,
  Radio,
  Row,
  Col
} from '@douyinfe/semi-ui';
import { 
  IconLink, 
  IconGithubLogo, 
  IconFile, 
  IconUpload, 
  IconCheckCircleStroked,
  IconSun, 
  IconMoon,
  IconSearch,
  IconCode,
  IconInfoCircle,
  IconBookStroked
} from '@douyinfe/semi-icons';
import PropTypes from 'prop-types';
import configData from './config.json';
import './App.css';

const { Content, Footer } = Layout;
const { TabPane } = Tabs;

// Import components
import AppHeader from './components/Header';
import ActionButtons from './components/ActionButtons';
import SearchBar from './components/SearchBar';
import ModelTable from './components/ModelTable';
import InfoCard from './components/InfoCard';
import NewsCard from './components/NewsCard';

function App() {
  const [data, setData] = useState([]);
  const [darkMode, setDarkMode] = useState(false);
  const [searchText, setSearchText] = useState('');
  const [activeConfig, setActiveConfig] = useState('default');
  const [scoreType, setScoreType] = useState('resolved');
  const contentRef = useRef(null);
  const leaderboardRef = useRef(null);
  const aboutRef = useRef(null);
  const citeRef = useRef(null);

  // Memoized values
  const currentConfig = useMemo(() => {
    return configData.evaluation_configs[activeConfig];
  }, [activeConfig]);

  const sortedData = useMemo(() => {
    if (!currentConfig || !currentConfig.models) return [];
    const scoreKey = scoreType === 'resolved' ? 'score_resolved' : 'score_well';
    return [...currentConfig.models].sort((a, b) => b[scoreKey] - a[scoreKey]);
  }, [currentConfig, scoreType]);

  const citeBibtex = useMemo(() => 
    `@article{${configData.meta.title.toLowerCase().replace(/ /g, '-')}-${configData.meta.year},
  title={${configData.content.cite.bibtex.title}},
  author={${configData.content.cite.bibtex.author}},
  journal={${configData.content.cite.bibtex.journal}},
  year={${configData.meta.year}},
  url={${configData.links.code}}
}`, [configData]);

  // Effects
  useEffect(() => {
    document.body.setAttribute('theme-mode', darkMode ? 'dark' : 'light');
  }, [darkMode]);

  useEffect(() => {
    setData(sortedData);
  }, [sortedData]);

  // Callbacks
  const handleThemeChange = useCallback((checked) => {
    setDarkMode(checked);
  }, []);

  const handleSearchChange = useCallback((value) => {
    setSearchText(value);
  }, []);

  const handleConfigChange = useCallback((configKey) => {
    setActiveConfig(configKey);
  }, []);

  const handleScoreTypeChange = useCallback((type) => {
    setScoreType(type);
  }, []);

  const handleScrollTo = useCallback((ref) => {
    console.log('handleScrollTo called with ref:', ref);
    if (!ref || !ref.current) {
      console.warn('Scroll target ref is null or undefined');
      return;
    }

    try {
      const element = ref.current;
      console.log('Scrolling to element:', element);
      if (!element) return;

      // 获取元素相对于文档的位置
      const elementRect = element.getBoundingClientRect();
      const absoluteElementTop = elementRect.top + window.pageYOffset;
      const headerOffset = 80; // 固定header高度 + 一些padding
      const scrollTop = absoluteElementTop - headerOffset;

      console.log('Scrolling to position:', scrollTop);
      window.scrollTo({
        top: scrollTop,
        behavior: 'smooth'
      });
    } catch (error) {
      console.error('Error scrolling to element:', error);
      // 最后的回退方案
      window.scrollTo(0, 0);
    }
  }, []);

  const configTabs = useMemo(() => {
    return Object.entries(configData.evaluation_configs).map(([key, config]) => ({
      itemKey: key,
      tab: config.name,
      name: config.name,
      description: config.description
    }));
  }, []);

  return (
    <ConfigProvider theme={darkMode ? { mode: 'dark' } : { mode: 'light' }}>
      <Layout className="app-layout">
        <AppHeader
          title={configData.meta.title}
          darkMode={darkMode}
          onThemeChange={handleThemeChange}
          onScrollTo={handleScrollTo}
          leaderboardRef={leaderboardRef}
          aboutRef={aboutRef}
          citeRef={citeRef}
        />

        <Content className="app-content" ref={contentRef}>
          <div className="content-wrapper">
            {/* Enhanced Action Buttons */}
            <Row justify="center" style={{ marginTop: 8, marginBottom: 16 }}>
              <Col>
                <Space size={16}>
                  <Button 
                    type="primary" 
                    size="large" 
                    icon={<IconLink />}
                    onClick={() => window.open(configData.links.paper, '_blank')}
                    style={{ 
                      borderRadius: 12,
                      fontSize: 15,
                      padding: '10px 20px',
                      boxShadow: '0 2px 8px rgba(0,0,0,0.1)'
                    }}
                  >
                    Paper
                  </Button>
                  <Button 
                    type="secondary" 
                    size="large" 
                    icon={<IconGithubLogo />}
                    onClick={() => window.open(configData.links.code, '_blank')}
                    style={{ 
                      borderRadius: 12,
                      fontSize: 15,
                      padding: '10px 20px',
                      boxShadow: '0 2px 8px rgba(0,0,0,0.1)'
                    }}
                  >
                    Code
                  </Button>
                  <Button 
                    type="secondary" 
                    size="large" 
                    icon={<IconFile />}
                    onClick={() => window.open(configData.links.data, '_blank')}
                    style={{ 
                      borderRadius: 12,
                      fontSize: 15,
                      padding: '10px 20px',
                      boxShadow: '0 2px 8px rgba(0,0,0,0.1)'
                    }}
                  >
                    Data
                  </Button>
                  <Button 
                    type="secondary" 
                    size="large" 
                    icon={<IconUpload />}
                    onClick={() => window.open(configData.links.submit, '_blank')}
                    style={{ 
                      borderRadius: 12,
                      fontSize: 15,
                      padding: '10px 20px',
                      boxShadow: '0 2px 8px rgba(0,0,0,0.1)'
                    }}
                  >
                    Submit
                  </Button>
                </Space>
              </Col>
            </Row>

            <Card className="leaderboard-card" ref={leaderboardRef} style={{ paddingTop: 4 }}>
              {/* Enhanced Configuration Tabs */}
              <div style={{ 
                marginBottom: 16,
                padding: '0 0 4px 0',
                borderBottom: '1px solid var(--semi-color-border)'
              }}>
                <div style={{ 
                  display: 'flex', 
                  justifyContent: 'space-between', 
                  alignItems: 'center',
                  marginBottom: 4
                }}>
                  <Tabs
                    type="line"
                    activeKey={activeConfig}
                    onChange={handleConfigChange}
                    style={{ 
                      marginBottom: 0,
                      flex: 1
                    }}
                  >
                    {configTabs.map(config => (
                      <TabPane 
                        tab={
                          <div style={{ 
                            display: 'flex', 
                            alignItems: 'center', 
                            gap: 6,
                            fontSize: 14,
                            fontWeight: 500
                          }}>
                            <span>{config.name}</span>
                          </div>
                        } 
                        itemKey={config.itemKey} 
                        key={config.itemKey}
                      />
                    ))}
                  </Tabs>
                  <SearchBar 
                    value={searchText}
                    onChange={handleSearchChange}
                    style={{ marginLeft: 16 }}
                  />
                </div>
                <Typography.Text 
                  type="secondary" 
                  style={{ 
                    fontSize: 12, 
                    lineHeight: 1.4,
                    display: 'block',
                    marginTop: 2
                  }}
                >
                  {currentConfig?.description}
                </Typography.Text>
              </div>

  

              <div className="table-container">
                <ModelTable
                  data={data}
                  searchText={searchText}
                  scoreType={scoreType}
                  onScoreTypeChange={handleScoreTypeChange}
                />
              </div>
            </Card>

            <div style={{ marginTop: 32 }}>
              <NewsCard news={configData.news} />
            </div>

            <div style={{ marginTop: 24 }} ref={aboutRef}>
              <InfoCard
                type="about"
                content={{
                  ...configData.content.about,
                  title: `About ${configData.meta.title}`
                }}
                links={configData.links}
              />
            </div>

            <div style={{ marginTop: 24 }} ref={citeRef}>
              <InfoCard
                type="cite"
                content={{
                  ...configData.content.cite,
                  bibtex: citeBibtex
                }}
                links={configData.links}
              />
            </div>
          </div>
        </Content>

        <Footer className="app-footer" role="contentinfo">
          <Space vertical align="center">
            <Typography.Text type="secondary">
              {configData.content.footer.partners}
            </Typography.Text>
            <Typography.Text type="secondary">
              © {configData.meta.year} {configData.meta.title}. All rights reserved.
            </Typography.Text>
          </Space>
        </Footer>
      </Layout>
    </ConfigProvider>
  );
}

export default App;
